import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'

import APP_ROUTES from './routes'

Vue.use(VueRouter);
const router = new VueRouter({ routes: APP_ROUTES, mode: 'history' });

Vue.config.productionTip = false

new Vue({
  render: h => h(App),
  router: router
}).$mount('#app')
